$startTranslateX: 50%;
$endTranslateX: 0;
$controlsWidth: 480px;

.player-controls {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: $gap-base;

	width: $controlsWidth;
	height: 100vh;
	padding-left: 5px;
	background: linear-gradient(
		270deg,
		rgba(0, 0, 0, 0.7) 0%,
		rgba(0, 0, 0, 0.511) 65.1%,
		rgba(0, 0, 0, 0.385) 79.69%,
		rgba(0, 0, 0, 0.063) 100%
	);

	&__toggle-btn {
		flex-shrink: 0;
	}

	&__pane {
		align-self: flex-start;
	}

	@include slideAnimation(400ms, $startTranslateX, $endTranslateX);
	@include fadeAnimation(400ms, 0.7, 1);
}

@media screen and (max-width: $mobile-width),
	screen and (max-height: $mobile-width) and (orientation: landscape),
	screen and (hover: none) and (pointer: coarse) and (max-width: $mobile-width),
	screen and (hover: none) and (pointer: coarse) and (max-height: $mobile-width) and (orientation: landscape) {
	.player-controls {
		justify-content: center;
		gap: 0;

		width: 100%;
		padding-left: 0;
	}

	button.player-controls__toggle-btn {
		display: none;
	}
}
