@mixin scrollbar {
	$width: 5px;
	$thumb-color: rgba(
		$color: $clr-primary-app,
		$alpha: 0.8,
	);
	$track-color: rgba(
		$color: $clr-primary-app,
		$alpha: 0.4,
	);

	::-webkit-scrollbar {
		width: $width;
	}

	::-webkit-scrollbar-track {
		background-color: $track-color;
	}

	::-webkit-scrollbar-thumb {
		border-radius: 5px;
		background-color: $thumb-color;
	}

	* {
		scrollbar-width: thin;
		scrollbar-color: $thumb-color $track-color;
	}
}

@mixin slideAnimation($duration, $startTranslateX, $endTranslateX) {
	.slide-animation-enter {
		transform: translateX($startTranslateX);
	}

	.slide-animation-enter-active {
		transform: translateX($startTranslateX);
		transition: transform $duration;
	}

	.slide-animation-enter-done {
		transform: translateX($endTranslateX);
		transition: transform $duration;
	}

	.slide-animation-exit {
		transform: translateX($endTranslateX);
	}

	.slide-animation-exit-active {
		transform: translateX($endTranslateX);
		transition: transform $duration;
	}

	.slide-animation-exit-done {
		transform: translateX($startTranslateX);
		transition: transform $duration;
	}
}

@mixin fadeAnimation($duration, $startScale, $endScale) {
	.fade-animation-enter {
		opacity: 0;
		transform: scale($startScale);
	}

	.fade-animation-enter-done {
		opacity: 1;
		transform: scale($endScale);
		transition: transform $duration, opacity $duration;
	}

	.fade-animation-exit {
		opacity: 0;
		transform: scale($startScale);
	}

	.fade-animation-exit-done {
		opacity: 1;
		transform: scale($endScale);
		transition: transform $duration, opacity $duration;
	}
}

@mixin popupAnimation(
	$duration,
	$startScale,
	$endScale,
	$startTranslateY,
	$endTranslateY
) {
	.popup-animation-enter {
		opacity: 0;
		transform: scale($startScale) translateY($startTranslateY);
	}

	.popup-animation-enter-done {
		opacity: 1;
		transform: scale($endScale) translateY($endTranslateY);
		transition: transform $duration, opacity $duration;
	}

	.popup-animation-exit {
		opacity: 1;
		transform: scale($endScale) translateY($endTranslateY);
	}

	.popup-animation-exit-active {
		opacity: 0;
		transform: scale($startScale) translateY($startTranslateY);
		transition: transform $duration, opacity $duration;
	}
}

@mixin fadeSlideAnimation(
	$duration,
	$startTranslateX,
	$endTranslateX,
	$startScale,
	$endScale
) {
	.fade-slide-animation-enter {
		opacity: 0;
		transform: scale($startScale) translateX($startTranslateX);
	}

	.fade-slide-animation-enter-done {
		opacity: 1;
		transform: scale($endScale) translateX($endTranslateX);
		transition: transform $duration, opacity $duration;
	}

	.fade-slide-animation-exit {
		opacity: 1;
		transform: scale($endScale) translateX($endTranslateX);
	}

	.fade-slide-animation-exit-done {
		opacity: 0;
		transform: scale($startScale) translateX($startTranslateX);
		transition: transform $duration, opacity $duration;
	}
}

@mixin disappearAnimation($duration) {
	.appear-animation-enter {
		opacity: 1;
	}

	.appear-animation-exit {
		opacity: 1;
	}

	.appear-animation-exit-active {
		opacity: 0;
		transition: opacity $duration;
	}
}
