@import '../../../../styles/variables';

.BaseTextInput {
	position: relative;

	min-height: 60px;
	padding: 10px 20px;
	border: 1px solid $clr-primary-app;
	border-radius: $border-radius-base;
	background-color: transparent;

	font-family: inherit;
	font-size: $font-size-small;
	color: $clr-primary-app;

	&::placeholder {
		font-family: inherit;
		color: rgba($clr-primary-app, 0.7);
	}
}
