@import './ChatHeader.scss';
@import './ChatForm.scss';
@import './ChatFormUser.scss';
@import './ChatFormMessage.scss';
@import './ChatActions.scss';
@import './ChatHistory.scss';
@import './ChatMessage.scss';
@import './ChatVoice.scss';
@import './ChatToggle.scss';

.chat {
	display: flex;
	flex-direction: column;

	width: 420px;

	&__wrapper {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		gap: $gap-base;

		// TODO: replace fixed height value to responsive
		max-height: 540px;
		// height: 80%;
		width: 100%;
		padding: 20px 10px;
		border-radius: $border-radius-base;
		background-color: rgba($color: $clr-secondary-app, $alpha: 0.7);
	}

	&__history {
		align-self: stretch;
	}

	&__form {
		align-self: stretch;
	}

	button.chat__toggle-chat-btn {
		border: none;
		background-color: rgba($color: $clr-secondary-app, $alpha: 0.4);
	}

	@include fadeAnimation(200ms, 0.8, 1);
	@include popupAnimation(200ms, 0.6, 1, 50%, 0);
}

.chat .chat__wrapper {
	@include popupAnimation(150ms, 0.8, 1, 50%, 0);
}

@media screen and (max-width: $mobile-width),
	screen and (max-height: $mobile-width) and (orientation: landscape),
	screen and (hover: none) and (pointer: coarse) and (max-width: $mobile-width),
	screen and (hover: none) and (pointer: coarse) and (max-height: $mobile-width) and (orientation: landscape) {
	.chat {
		width: 100%;

		&__wrapper {
			max-height: 100vh;
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
		}

		&__toggle {
			display: none;
		}
	}
}
