.chat-form-user {
	display: flex;
	flex-direction: column;
	gap: $gap-medium;

	&__send-btn {
		align-self: flex-end;

		min-width: 120px;
		min-height: 40px;
		border: 1px solid rgba($color: $clr-primary-app, $alpha: 0.7);

		font-size: $font-size-base;
	}

	&__label {
		position: relative;
		display: block;
	}

	&__input {
		min-height: 60px;
		width: 100%;
		padding: 10px 20px;
		border: 1px solid $clr-primary-app;
		border-radius: $border-radius-base;
		background-color: transparent;

		font-family: inherit;
		font-size: $font-size-small;
		color: $clr-primary-app;

		&::placeholder {
			font-family: inherit;
			color: rgba($clr-primary-app, 0.7);
		}
	}

	&__error {
		position: absolute;
		bottom: -30px;
		left: 0;

		font-size: $font-size-small;
		color: $clr-error;
	}
}
