.swipe-hints {
	display: flex;
	flex-direction: column;
	justify-content: space-between;

	height: 100%;
	max-width: $mobile-width;
	margin: 0 auto;
	padding: 60px 20px 40px 40px;

	&__container {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: $gap-small;

		max-width: 220px;

		&--1 {
			align-self: flex-end;
		}

		&--2 {
			align-self: center;
		}
	}

	&__hint {
		display: block;

		font-size: $font-size-base;
		line-height: 35px;
		color: $clr-primary-app;
	}

	@include fadeSlideAnimation(400ms, 50%, 0, 0.8, 1);
}

.swipe-hints-overlay {
	display: none;
}

@include disappearAnimation(1000ms);

// TODO: Detect does device have mobile type with touch support in JS
@media screen and (max-width: $mobile-width),
	screen and (max-height: $mobile-width) and (orientation: landscape),
	screen and (hover: none) and (pointer: coarse) and (max-width: $mobile-width),
	screen and (hover: none) and (pointer: coarse) and (max-height: $mobile-width) and (orientation: landscape) {
	.swipe-hints-overlay {
		display: block;
	}
}
