@import './PlayerVideo.scss';
@import './PlayerControls.scss';
@import './PlayerControlsPane.scss';
@import './PlayerControlsItem.scss';

$startTranslateX: 75%;
$startTranslateXMobile: 100%;
$endTranslateX: 0;

.player {
	position: relative;
	height: 100%;

	&__video {
		width: 100vw;
		height: 100%;
	}

	&__controls {
		position: absolute;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 2147483647;

		max-height: 100vh;
		transform: translateX($startTranslateX);
	}

	@include slideAnimation(400ms, $startTranslateX, $endTranslateX);
}

@media screen and (max-width: $mobile-width),
	screen and (max-height: $mobile-width) and (orientation: landscape),
	screen and (hover: none) and (pointer: coarse) and (max-width: $mobile-width),
	screen and (hover: none) and (pointer: coarse) and (max-height: $mobile-width) and (orientation: landscape) {
	.player {
		&__controls {
			transform: translateX($startTranslateXMobile);
		}

		@include slideAnimation(300ms, $startTranslateXMobile, $endTranslateX);
	}
}
