.player-controls-pane {
	height: 100%;
	padding: 60px 40px 80px 10px;
	overflow: auto;

	&__logo {
		display: block;
		margin-left: auto;
		margin-bottom: $gap-medium;
	}

	&__list {
		display: flex;
		flex-direction: column;
		gap: $gap-medium;
	}

	button.change-avatar-btn:not(:focus-visible) {
		border: none;
		outline: 3px solid rgba($clr-primary-app, 0.5);
	}

	button.change-avatar-btn--active:not(:focus-visible) {
		outline: 3px solid $clr-primary-app;
	}

	.avatar-img {
		border-radius: 50%;
		width: 100%;
		height: 100%;
	}
}

@media screen and (max-width: $mobile-width),
	screen and (max-height: $mobile-width) and (orientation: landscape),
	screen and (hover: none) and (pointer: coarse) and (max-width: $mobile-width),
	screen and (hover: none) and (pointer: coarse) and (max-height: $mobile-width) and (orientation: landscape) {
	.player-controls-pane {
		display: flex;
		flex-direction: column;
		align-items: center;

		width: 100%;
		padding: 60px 20px 40px 20px;
	}
}
