// Adaptivity
$mobile-iphone4-width: 320px;
$mobile-width: 480px;
$tablet-width: 860px;

$gap-base: 20px;
$gap-small: calc($gap-base / 2);
$gap-medium: calc($gap-base * 2);
$gap-large: calc($gap-base * 4);

$border-radius-base: 10px;

// Colors
$clr-primary-app: white;
$clr-secondary-app: black;
$clr-voice-input: #ff4545;
$clr-message-bg-primary: #d9d9d9;
$clr-message-bg-secondary: #437eb5;
$clr-error: #fd4b4b;

// Fonts
$font-size-large: 22px;
$font-size-base: 18px;
$font-size-small: 16px;

:export {
	mobileWidth: $mobile-width;
}
