.LoaderCircle {
	position: absolute;
	top: 50%;
	left: 50%;

	display: flex;
	justify-content: center;
	align-items: center;

	width: 80px;
	height: 80px;
	transform: translate(-50%, -50%);

	&::after {
		content: ' ';
		display: block;
		width: 65px;
		height: 65px;

		border-radius: 50%;
		border: 6px solid white;
		border-color: white rgba($color: black, $alpha: 0.6) white
			rgba($color: black, $alpha: 0.6);

		animation: spin 1s linear infinite;
	}
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
