.chat-history {
	display: flex;
	flex-direction: column;
	gap: $gap-base;

	padding: 5px;
	overflow-y: auto;

	&__toggle {
		display: none;
	}

	&__messages-list {
		display: none;

		&--active {
			display: flex;
			flex-direction: column;
			gap: $gap-base;
			overflow-y: auto;
		}
	}

	&__message {
		align-self: flex-start;
		flex-shrink: 0;

		&--from-me {
			align-self: flex-end;
		}
	}
}

@media screen and (max-width: $mobile-width),
	screen and (max-height: $mobile-width) and (orientation: landscape),
	screen and (hover: none) and (pointer: coarse) and (max-width: $mobile-width),
	screen and (hover: none) and (pointer: coarse) and (max-height: $mobile-width) and (orientation: landscape) {
	.chat-history {
		&__toggle {
			display: flex;
			justify-content: center;
			align-items: center;
			gap: $gap-small;
		}
	}
}
