.chat-form-message {
	display: flex;
	flex-direction: column;
	gap: $gap-small;

	&__send-btn {
		align-self: flex-end;

		min-width: 120px;
		min-height: 40px;
		border: 1px solid rgba($color: $clr-primary-app, $alpha: 0.7);

		font-size: $font-size-base;
	}

	&__container {
		display: flex;
		gap: $gap-small;
	}

	&__text-input {
		flex-grow: 1;
	}
}
